// Order Management
export const createOrder = async (orderData: Omit<Order, 'id'>) => {
  try {
    const docRef = await addDoc(collection(db, 'orders'), {
      ...orderData,
      createdAt: serverTimestamp(),
    });
    return docRef.id;
  } catch (error) {
    console.error('Error creating order:', error);
    throw error;
  }
};

export const updateOrderStatus = async (orderId: string, status: Order['status'], paymentId?: string) => {
  try {
    const updates: any = { status };
    if (paymentId) {
      updates.paymentId = paymentId;
    }
    if (status === 'completed') {
      updates.completedAt = serverTimestamp();
    }
    
    await updateDoc(doc(db, 'orders', orderId), updates);
    
    // If order is completed, update book sales and user purchased books
    if (status === 'completed') {
      const orderDoc = await getDoc(doc(db, 'orders', orderId));
      if (orderDoc.exists()) {
        const order = orderDoc.data() as Order;
        
        // Update each book's sales and revenue
        for (const item of order.books) {
          await updateDoc(doc(db, 'books', item.bookId), {
            sales: increment(item.quantity),
            revenue: increment(item.price * item.quantity),
            stock: increment(-item.quantity)
          });
          
          // Get book to update author stats
          const bookDoc = await getDoc(doc(db, 'books', item.bookId));
          if (bookDoc.exists()) {
            const book = bookDoc.data() as Book;
            await updateDoc(doc(db, 'users', book.authorId), {
              totalSales: increment(item.quantity),
              totalRevenue: increment(item.price * item.quantity)
            });
          }
        }
        
        // Update user's purchased books
        const purchasedBookIds = order.books.map(item => item.bookId);
        const userDoc = await getDoc(doc(db, 'users', order.userId));
        if (userDoc.exists()) {
          const user = userDoc.data() as User;
          const updatedPurchasedBooks = [...(user.purchasedBooks || []), ...purchasedBookIds];
          await updateDoc(doc(db, 'users', order.userId), {
            purchasedBooks: updatedPurchasedBooks
          });
        }
      }
    }
  } catch (error) {
    console.error('Error updating order status:', error);
    throw error;
  }
};

