// Review Management
export const createReview = async (reviewData: Omit<Review, 'id'>) => {
  try {
    const docRef = await addDoc(collection(db, 'reviews'), {
      ...reviewData,
      createdAt: new Date().toISOString(),
    });
    
    // Update book's review stats
    const bookReviews = await getBookReviews(reviewData.bookId);
    const totalRating = bookReviews.reduce((sum, review) => sum + review.rating, 0) + reviewData.rating;
    const totalReviews = bookReviews.length + 1;
    const averageRating = totalRating / totalReviews;
    
    await updateDoc(doc(db, 'books', reviewData.bookId), {
      totalReviews: totalReviews,
      averageRating: averageRating
    });
    
    return docRef.id;
  } catch (error) {
    console.error('Error creating review:', error);
    throw error;
  }
};

