// Follow Management
export const followAuthor = async (readerId: string, authorId: string) => {
  try {
    // Check if already following
    const q = query(
      collection(db, 'follows'),
      where('readerId', '==', readerId),
      where('authorId', '==', authorId)
    );
    const existingFollow = await getDocs(q);
    
    if (!existingFollow.empty) {
      throw new Error('Already following this author');
    }
    
    const docRef = await addDoc(collection(db, 'follows'), {
      readerId,
      authorId,
      followedAt: serverTimestamp(),
    });
    
    // Update reader's followed authors
    const userDoc = await getDoc(doc(db, 'users', readerId));
    if (userDoc.exists()) {
      const user = userDoc.data() as User;
      const updatedFollowedAuthors = [...(user.followedAuthors || []), authorId];
      await updateDoc(doc(db, 'users', readerId), {
        followedAuthors: updatedFollowedAuthors
      });
    }
    
    return docRef.id;
  } catch (error) {
    console.error('Error following author:', error);
    throw error;
  }
};

export const unfollowAuthor = async (readerId: string, authorId: string) => {
  try {
    const q = query(
      collection(db, 'follows'),
      where('readerId', '==', readerId),
      where('authorId', '==', authorId)
    );
    const followDocs = await getDocs(q);
    
    if (!followDocs.empty) {
      await deleteDoc(followDocs.docs[0].ref);
      
      // Update reader's followed authors
      const userDoc = await getDoc(doc(db, 'users', readerId));
      if (userDoc.exists()) {
        const user = userDoc.data() as User;
        const updatedFollowedAuthors = (user.followedAuthors || []).filter(id => id !== authorId);
        await updateDoc(doc(db, 'users', readerId), {
          followedAuthors: updatedFollowedAuthors
        });
      }
    }
  } catch (error) {
    console.error('Error unfollowing author:', error);
    throw error;
  }
};

